<template>
  <section>
    <div class="container mx-auto px-3">
      <div class="grid md:grid-cols-2 lg:grid-cols-3 -mx-3">
        <div
          class="col-span-1 px-3 mb-5"
          v-for="(lawyer, index) in 18"
          :key="index"
          data-aos="fade-up"
        >
          <lawyer-card />
        </div>
      </div>
      <div class="pagination my-8">
        <div class="pagination-wrap">
          <div class="prev"></div>
          <div class="count">2 of 119</div>
          <div class="next"></div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import lawyerCard from "./cards/lawyerCard.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: { lawyerCard },
  computed: {
    ...mapState({
      contList: (state) => state.contragents.list,
    }),
  },
  methods: {
    ...mapActions({
      getContragents: "contragents/getList",
    }),
    async mountedStart() {
      let dataPage = {
        querys: {
          search: "",
          status: "",
          contragentId: "",
          regionId: "",
        },
        data: {
          page: 0,
          limit: 20,
        },
      };
      await this.getContragents(dataPage);
    },
  },
  created() {
    this.mountedStart();
  },
};
</script>

<style scoped lang="scss">
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &-wrap {
    display: flex;
    .prev,
    .next {
      display: flex;
      width: 45px;
      height: 45px;
      justify-content: center;
      align-items: center;
      border: 1px solid #d4d4d4;
      cursor: pointer;
    }
    .count {
      display: flex;
      padding: 0 20px;
      justify-content: center;
      align-items: center;
      border: 1px solid #d4d4d4;
      font-size: 14px;
      color: black;
    }

    .prev {
      background: url("@/assets/img/blue-arrow1.svg") no-repeat center center;
    }
    .next {
      background: url("@/assets/img/blue-arrow2.svg") no-repeat center center;
    }
  }
}
</style>
